/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, tt, var,
center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
  vertical-align: baseline;
  box-sizing: border-box;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	/* list-style: none; */
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

html, body {
  height: 100%;
  font-size: 10px;
}



/* Base rules */
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-size: 1.6rem;
}

/* Whatevs, just some styling */
input {
  width: 100%;
  font-size: 1.6rem;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #9e9e9e;
  border-radius: 0;
  outline: none;
  height: 2rem;
  padding: 0 0 0 0.5rem;
  box-sizing: border-box;
}

input[type="range"] {
  padding: 0 0.5rem;
  margin: 0;
  border: 0;
  direction: rtl;
}

label {
  margin-bottom: 2px;
}

.input-group {
  overflow: hidden;
  margin-bottom: 20px;
}

.input-group #input {
  overflow: hidden; 
}

.input-group #inline-submit {
  float: right;
  margin-left: 1.5rem;
}

.icon-button:hover {
  background-color: #d2d2d2;
}

.icon-button {
  display: table; /* https://stackoverflow.com/a/16629593/4389332 */
  border-radius: 2px;
  text-align: center;
  padding: 0 0.5rem 0;
  height: 20px;
  width: 20px;
  cursor: pointer;
}

.icon-button i {
  display: table-cell;
  vertical-align: middle;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    /* display: none; <- Crashes Chrome on hover */
    -webkit-appearance: none;
    margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}

.row {
  margin: 0 -10px;
}

/* Grid system */
.row:before, 
.row:after {
  content:"";
  display: table;
  clear:both;
}

[class*='col-'] {
  float: left;
  min-height: 1px; 
  width: 33.33%;
  padding: 0 10px;
}

.col-1 {
  width: 33.33%; 
}

.col-2 {
  width: 50%; 
}

.col-3 {
  width: 100%; 
}
.game-of-life__boat-cell {
  position: absolute;
  background-color: black;
  width: 12px;
  height: 12px;
}

.game-of-life__boat-cell--1 {
  top: 14px;
  left: 14px;
}

.game-of-life__boat-cell--2 {
  top: 14px;
  left: 26px;
}

.game-of-life__boat-cell--3 {
  top: 26px;
  left: 14px;
}

.game-of-life__boat-cell--4 {
  top: 26px;
  left: 38px;
}

.game-of-life__boat-cell--5 {
  top: 38px;
  left: 26px;
}
.game-of-life__controls {
	/*position: fixed;*/
	/*bottom: 3rem;*/
	/*right: 3rem;*/
}

.game-of-life__toggle {
	z-index: 1;
	position: fixed;
	bottom: 3rem;
	right: 3rem;
	cursor: pointer;
	width: 60px;
	height: 60px;
	background: #6d6f6f;
	color: #00A3DD;
	border-radius: 50%;
	box-shadow: 0 0 5px rgba(0,0,0,0.15);
}

.game-of-life__panel {
	z-index: 2;	
	box-shadow: 0 0 5px rgba(0,0,0,0.15);
	position: fixed;
	bottom: 3rem;
	right: 3rem;
	width: 340px;
	text-align: left;
	overflow: hidden;
	visibility: hidden;
	-webkit-transform: scale(0);
	        transform: scale(0);
	-webkit-transform-origin: 100% 100%;
	        transform-origin: 100% 100%;
	transition: visibility 0s 0.2s, opacity 0.2s, -webkit-transform 0.2s;
	transition: transform 0.2s, visibility 0s 0.2s, opacity 0.2s;
	transition: transform 0.2s, visibility 0s 0.2s, opacity 0.2s, -webkit-transform 0.2s;
	opacity: 0;

  background-color: white;
  border-radius: 4px;
  padding: 1rem;
}

.game-of-life__panel > * {
	margin-bottom: 15px;
}

.game-of-life__panel > *:last-child {
	margin-bottom: 5px;
}

@media only screen and (max-width: 992px) {
	.game-of-life__toggle {
		bottom: 1rem;
		right: 1rem;
	}
	.game-of-life__panel {
		bottom: 1rem;
		right: 1rem;
	}
}

.game-of-life__panel:focus {
  outline: medium none invert;
  outline: initial; 
}

.game-of-life__panel.active {
	height: auto;
	opacity: 1;
	visibility: visible;
	-webkit-transform: scale(1);
	        transform: scale(1);
	transition: visibility 0s 0s, opacity 0.2s, -webkit-transform 0.2s;
	transition: transform 0.2s, visibility 0s 0s, opacity 0.2s;
	transition: transform 0.2s, visibility 0s 0s, opacity 0.2s, -webkit-transform 0.2s;
}

.game-of-life__panel-header {
	font-size: 1.5rem;
	margin-bottom: 15px;
}

.game-of-life__panel-header > i {
	padding: 0 5px;
}

.game-of-life__controls label {
		display: block;
    font-size: 1.2rem;
    padding-bottom: 0.2rem;
}
.game-of-life-wrapper {
  overflow: hidden;
}

.game-of-life-wrapper > canvas {
  pointer-events: none;
  height: 100vh;
  width: 100vw;
}
